import React from 'react';
import {
  Form,
  Input,
  Select,
  Row,
  Col,
  Checkbox,
  Button,
} from 'antd';
import './signup.scss';

const { Option } = Select;

const formItemLayout = {
  labelCol: {
    xs: {
      span: 10,
    },
    sm: {
      span: 10,
    },
  },
  wrapperCol: {
    xs: {
      span: 14,
    },
    sm: {
      span: 14,
    },
  },
};

const tailFormItemLayout = {
  wrapperCol: {
    xs: {
      span: 24,
      offset: 0,
    },
    sm: {
      span: 16,
      offset: 8,
    },
  },
};

const SignUp = () => {
  const [form] = Form.useForm();

  const onFinish = (values) => {
    console.log('Received values of form: ', values);
  };

  const prefixSelector = (
    <Form.Item name="prefix" noStyle>
      <Select
        style={{
          width: 70,
        }}
      >
        <Option value="57">+57</Option>
      </Select>
    </Form.Item>
  );

  return (
    <Row className="sign-up-container">
      <Col xs={24}>
        <h1>Registro</h1>
      </Col>
      <Col xs={24}>
        <Form
          {...formItemLayout}
          form={form}
          name="register"
          onFinish={onFinish}
          initialValues={{
            prefix: '57',
          }}
          scrollToFirstError
        >
          <Form.Item
            name={'name'}
            label="Nombre completo"
            rules={[
              {
                required: true,
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="identification"
            label="Cédula de ciudadanía"
            rules={[
              {
                required: true,
                message: 'Inserta tu número de identificación!',
                whitespace: true,
              },
              () => ({
                validator(rule, value) {
                  const reg = /[0-9]{6,10}$/;
                  if (reg.exec(value)) return Promise.resolve();;
                  return Promise.reject('Mínimo 6 números, máximo 10.');
                },
              }),
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="email"
            label="Correo electrónico"
            rules={[
              {
                type: 'email',
                message: 'The input is not valid E-mail!',
              },
              {
                required: true,
                message: 'Please input your E-mail!',
              },
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="password"
            label="Contraseña"
            rules={[
              {
                required: true,
                message: 'Please input your password!',
              },
            ]}
            hasFeedback
          >
            <Input.Password />
          </Form.Item>

          <Form.Item
            name="confirm"
            label="Confirmar contraseña"
            dependencies={['password']}
            hasFeedback
            rules={[
              {
                required: true,
                message: 'Please confirm your password!',
              },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (!value || getFieldValue('password') === value) {
                    return Promise.resolve();
                  }

                  return Promise.reject('The two passwords that you entered do not match!');
                },
              }),
            ]}
          >
            <Input.Password />
          </Form.Item>
          <Form.Item
            name="phone"
            label="Celular"
            rules={[
              {
                required: true,
                message: 'Por favor ingresa tu celular!',
              },
            ]}
          >
            <Input
              addonBefore={prefixSelector}
              style={{
                width: '100%',
              }}
            />
          </Form.Item>
          <Form.Item
            name="referidNumber"
            label="Número de referido"
            rules={[
              {
                required: false,
                message: 'Inserta tu número de referido',
                whitespace: true,
              },
              () => ({
                validator(rule, value) {
                  const reg = /[0-9]{6,10}$/;
                  if (reg.exec(value)) return Promise.resolve();;
                  return Promise.reject('Mínimo 6 números, máximo 10.');
                },
              }),
            ]}
          >
            <Input />
          </Form.Item>
          <Form.Item
            name="agreement"
            valuePropName="checked"
            rules={[
              {
                validator: (_, value) =>
                  value ? Promise.resolve() : Promise.reject('Acepta los términos y condiciones por favor'),
              },
            ]}
            {...tailFormItemLayout}
          >
            <Checkbox>
              Estoy de acuerdo con los <a href="">términos y condiciones</a>
            </Checkbox>
          </Form.Item>
          <Form.Item {...tailFormItemLayout}>
            <Button type="primary" htmlType="submit">
              Registrarse
        </Button>
          </Form.Item>
        </Form>
      </Col>
    </Row>
  );
};

export default SignUp;